.left{
    float: left;
}
.right{
    float: left;
}
.title{
    text-align: center;
    margin-bottom: 30px;
}
.title h2{
    font-size: 26px;
    font-weight: bold;
   padding-bottom: 13px;
}
.title p{
    font-size: 12px;
    color: #005bac;
    font-weight: bold;
}
.title span{
    width: 20px;
    height: 4px;
    display: inline-block;
    background: #005bac;
}
.news ul li{
    float: left;
    margin-right: 39px;
    width: 270px;
}
.news ul li div{
    width: 270px;
    height: 180px;
    margin-bottom: 16px;
}
.news ul li span{
    font-size: 14px;
    margin-bottom: 10px;
    display: block;
    line-height: 14px;
}
.news ul li p{
    font-size: 14px;
    font-weight: bold;
    line-height: 14px;
}
.news ul li:last-child{
    margin-right: 0;
}
.news ul li:first-child div{
    background: url(../img/newpic.jpg) no-repeat left top/cover;
}
.news ul li:nth-child(2) div{
    background: url(../img/newpic2.jpg) no-repeat left top/cover;
}
.news ul li:nth-child(3) div{
    background: url(../img/newpic3.jpg) no-repeat left top/cover;
}
.news ul li:last-child div{
    background: url(../img/newpic4.jpg) no-repeat left top/cover;
}
.more{
    width: 140px;
    height: 40px;
    border-radius: 5px;
    background: #005bac;
    display: block;
    margin: 40px auto;
    text-align: center;
    color: #fff;
    font-size: 14px;
    line-height: 40px;
}
.more i{
    font-family: iconfont;
    font-size: 20px;
    float: left;
    margin-left: 28px;
}


