.hd_wrap{
    width: 1200px;
    margin: 0 auto;
    font-size: 14px;
}
.hd_left{
    float: left;
}
.hd_right{
    float: right;
    font-family: 'iconfont'
}
.nav{
    float: left;
}
.nav li{
    float: left;
    line-height: 52px;
    position: relative;
}
.nav li span{
    padding: 0 22px;
    border: solid #d7d7d7;
    border-width: 0 1px 0 1px;
    margin-left: -1px;
}
.nav li i{
    width: 14px;
    height: 16px;
    line-height: 16px;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -7px;
}
.nav li:hover{
    color:#f33f08;
}
.nav li:hover i{
    -webkit-transition: .3s all ease-out;
    -o-transition: .3s all ease-out;
    transition: .3s all ease-out;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
}
.hd_lang{
    float: left;
    margin-top: 18px;
    height: 16px;
    line-height: 16px;
    overflow: hidden;
    padding: 0 22px;
    color: #2070b6;
}
.hd_right button{
    float: left;
    width: 28px;
    height: 28px;
    font-size: 28px;
    margin-top: 10px;
    font-family: 'iconfont';
    color: #2070b6;
}


