
.profile_wrap{
    background: url(../img/bg.jpg);
    padding: 20px 0;
}
.profile{
    width: 1200px;
    margin: 0 auto;
    background: rgba(255,255,255,.9);
}   
.title{
    text-align: center;
    padding: 34px 0 31px 0;
}
.title h4{
    font-size: 26px;
    color: #333333;
    padding-bottom: 12px;
}
.title p{
    color: #005bac;
    font-size: 12px;
    padding-bottom: 5px;
}
.title span{
    display:block;
    width: 20px;
    height: 4px;
    margin: 0 auto;
    background: #005bac;
}
.con_padding{
    margin: 0 50px;
    
}
.con{
    padding-bottom: 42px;
    position: relative;
}
.con_left{
    width: 500px;
    height: 330px;
    background: #ff0;
    float: left;
    background: url(../img/con-img.jpg) no-repeat;
    background-size: cover;
}
.con_text{
    margin-left: 543px;
    line-height: 26px;
}
.con_text span{
    display: block;
    height: 26px;
}
.btn{
    width: 140px;
    height: 40px;
    line-height: 40px;
    color: #fff;
    background: #005bac;
    text-align: center;
    font-size: 14px;
    position: absolute;
    left: 593px;
    bottom: 42px;

}
.con_text i{
    font-family: 'iconfont';
    display: inline-block;
    width: 18px;
    height: 18px;
    line-height: 18px;
    font-size: 18px;
    margin-right: 8px;
    vertical-align: middle;
}




