.u_rdcenter{
    width: 100%;
    padding: 60px 0;
}
.u_rdcenter_main{
    width: 1200px;
    margin: 0 auto;
}
.u_rdcenter_top h4{
    padding-bottom: 12px;
    color: #333333;
    text-align: center;
    font-size: 26px;
}
.u_rdcenter_top p{
    padding-bottom: 6px;
    color: #005bac;
    text-align: center;
}
.u_rdcenter_top span{
    display: block;
    margin: 0 auto;
    width: 20px;
    height: 4px;
    background-color: #005bac;
}
.u_rdcenter_con{
    margin-top: 30px;
}
.u_rdcenter_supcon{
    width: 560px;
    float: left;
    border: solid 1px #dfdfdf;
    padding: 5px;
    margin-right: 59px;
    margin-bottom: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
}
.u_rdcenter_supcon_img1,.u_rdcenter_supcon_img2,.u_rdcenter_supcon_img3,.u_rdcenter_supcon_img4{
    width: 240px;
    height: 150px;
    float: left;
}
.u_rdcenter_supcon_img1{
    background: url('../images/research_1.png') center;
}
.u_rdcenter_supcon_img2{
    background: url('../images/research_2.png') center;
}
.u_rdcenter_supcon_img3{
    background: url('../images/research_3.png') center;
}
.u_rdcenter_supcon_img4{
    background: url('../images/research_4.png') center;
}

.u_rdcenter_supcon_con{
    margin-left: 15px;
    float: left;
}
.u_rdcenter_supcon_con h2{
    margin-top: 47px;
    font-size: 26px;
    color: #333333;
    font-weight: normal;
    margin-bottom: 6px;
}
.u_rdcenter_supcon_con p{
    font-size: 16px;
    color: #666666;
}
.u_rdcenter_supcon_son{
    width: 38px;
    height: 38px;
    line-height: 38px;
    background-color: #005bac;
    position: absolute;
    bottom: 10px;
    right: -20px;
    text-align: center;
}
.u_rdcenter_supcon_son i{
    display: inline-block;
    font-family: 'iconfont'; 
    width: 18px;
    height: 18px;
    font-size: 20px;
    color: white;
}
.u_rdcenter_supconten{
    margin-right: 21px;
}
.u_rdcenter_supcon_read{
    width: 140px;
    height: 40px;
    margin: 0 auto;
    background-color: #005bac;
    margin-top: 10px;
    border-radius: 3px;
}
.u_rdcenter_supcon_read a{
    display: block;
    text-align: center;
    line-height: 40px;
}
.u_rdcenter_supcon_read a span{
    font-size: 14px;
    color: #fff;
}
.u_rdcenter_supcon_read a span i{
    font-family: 'iconfont'; 
    padding-right: 11px;
    font-size: 14px;
} 


