@charset "utf-8";
@import "./iconfont.css";
* {
	margin: 0;
	padding: 0;
	list-style: none;
	font-weight: normal;
	font-style: normal;
}

a {
	text-decoration: none;
}

img {
	border: none;
}
input{
    outline: none;
}
.clearfix:after,
.clearfix:before {
	content: '';
	display: table;
}

.clearfix:after {
	clear: both;
}

.clearfix {
	*zoom: 1;
}
.left{
	float: left;
}
.right{
	float: right;
}
.concent{
	width: 1200px;
	margin: 0 auto;
}


