.left{
    float: left;
}
.right{
    float: left;
}
.business{
    margin: 40px 0 80px;
}
.businessContent{
    border-top: 2px solid #005bac;
    border-bottom: 2px solid #005bac;
}
.font{
    width: 320px;
    float: left;
    padding: 50px 0;
    margin-right: 58px;
}
.font h4{
    font-size: 26px;
    font-weight: 800px;
    text-align: center; 
    line-height: 46px;
}
.font p{
    font-size: 18px;
    text-align: center; 
    line-height: 36px;
}
.businessContent ul{
    float: left;
     margin-left: 35px;
}
.businessContent ul li{
    float: left;
    width: 150px;
    padding-top: 37px;
    padding-bottom: 20px;
}
.businessContent ul li div{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    text-align: center;
    line-height: 100px;
    background: #005bac;
    margin: 0 auto;
}
.businessContent ul li div i{
    font-family: iconfont;
    color: #ffffff;
    font-size: 50px;
}
.businessContent ul li p{
    padding-top: 11px;
    text-align: center;
    font-size: 14px;
}


