.footer_main{
    width: 100%;
    height: 260px;
    background-color: #efefef;
    min-width: 1200px;
}
.footer_center{
    width: 1200px;
    height: 260px;
    margin: 0 auto;
}
.footer_center>ul{
    width: 95px;
    padding-top: 40px;
    float: left;
    margin-left:75px;
}
.footer_center>ul>h3{
    font-size: 16px;
    color: #333333;
    line-height: 37px;
position: relative;
}
.footer_center>ul>h3>i{
    width: 18px;
    height: 18px;
    color: #005bac;
     font-family: 'iconfont';
    display: block;
position: absolute;
left:-25px;
top:0;
   
}
.footer_center>ul>li{
    font-size: 14px;
    color: #666666;
    line-height: 32px;
    cursor: pointer;
}
.copy_foot{
    width: 100%;
    height: 40px;
    background-color: #005bac;
        min-width: 1200px;

}
.copy_center{
    width: 1200px;
    margin:0 auto;
    height: 40px;
}
.left_font{
    float: left;
    width: 951px;
    line-height: 40px;
    color: #fff;
    font-size: 12px;
    text-indent: 21px;
}
.right_list{
    float: left;
    width: 240px;
    height: 40px;
    
    color: #fff;
    font-size: 12px;
}
.right_list>a{
    display: inline-block;
    color: #fff;
    font-size: 12px;
    margin: 0px 12px;
    line-height: 40px

}



