.slide{
    height: 758px;
    background: url(../img/banner1.jpg) no-repeat center center/cover;
    position: relative;
}
.slide div{
    width: 100%;
    height: 200px;
    padding-top: 54px;
    background: url(../img/bg_02.png) no-repeat center center/cover;
    position: absolute;
    left: 0;top: 228px;

}
.slide div p{
    margin-left: 23.125%;
    margin-top: 45px;
    margin-bottom: 24px;
    font-size: 32px;
    color: #fff;
}
.slide div h3{
    margin-left: 23.125%;
    font-size: 50px;
    color: #fff;
}
.dots_wrap{
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    text-align: center;
}
.dots_wrap li{
    display: inline-block;
}
.dots_wrap li button{
    width: 40px;
    height: 4px;
    display: inline-block;
    margin: 0 10px;
    font-size: 0;
    outline: none;
}
.dots_wrap li button:before{
    content: '';
    width: 100%;
    height: 100%;
    background: #005bac;
}



